const difficultyColors = {
  beginner: 'bg-green-100 text-green-800',
  intermediate: 'bg-yellow-100 text-yellow-800', 
  advanced: 'bg-red-100 text-red-800'
};

const difficultyLabels = {
  beginner: '',
  intermediate: '',
  advanced: ''
};

export default function WorkflowsPage() {
  const [selectedWorkflow, setSelectedWorkflow] = useState<Workflow | null>(null);
  const [activeTab, setActiveTab] = useState('all');
  const [filteredWorkflows, setFilteredWorkflows] = useState(workflows);
  const { trackWorkflowView, trackCategoryFilter, trackUserAction } = useAnalytics();

  useEffect(() => {
    if (activeTab === 'all') {
      setFilteredWorkflows(workflows);
    } else {
      setFilteredWorkflows(workflows.filter(w => w.category === activeTab));
    }
  }, [activeTab]);

  const categories = [
    { id: 'all', name: '', count: workflows.length },
    { id: 'social-media', name: '', count: workflows.filter(w => w.category === 'social-media').length },
    { id: 'ecommerce', name: '', count: workflows.filter(w => w.category === 'ecommerce').length },
    { id: 'content-creation', name: '', count: workflows.filter(w => w.category === 'content-creation').length },
    { id: 'product-management', name: '', count: workflows.filter(w => w.category === 'product-management').length }
  ];

  if (selectedWorkflow) {
    return <WorkflowDetail workflow={selectedWorkflow} onBack={() => setSelectedWorkflow(null)} />;
  }

  return (
    <>
      <SEOHead 
        title="AI -  | WSNAIL.COM"
        description="AI"
        keywords="AI,,,,,,,WSNAIL"
        url="https://wsnail.com/workflows"
      />
      
      <div className="min-h-screen bg-gray-50">
        {/* Header */}
        <div className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
            <motion.div 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-center mb-8"
            >
              <h1 className="text-4xl font-bold text-gray-900 mb-4">
                AI
              </h1>
              <p className="text-xl text-gray-600 mb-8">
                AI
              </p>
              
              <div className="flex justify-center space-x-8 text-sm text-gray-600">
                <div className="flex items-center space-x-2">
                  <Zap className="w-5 h-5 text-blue-500" />
                  <span>60%+</span>
                </div>
                <div className="flex items-center space-x-2">
                  <Users className="w-5 h-5 text-green-500" />
                  <span>10+</span>
                </div>
                <div className="flex items-center space-x-2">
                  <Star className="w-5 h-5 text-yellow-500" />
                  <span>4.9</span>
                </div>
              </div>
            </motion.div>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {/* Categories */}
          <Tabs value={activeTab} onValueChange={(value) => {
            trackCategoryFilter(value);
            setActiveTab(value);
          }} className="mb-8">
            <TabsList className="grid w-full grid-cols-5">
              {categories.map(category => (
                <TabsTrigger key={category.id} value={category.id}>
                  {category.name} ({category.count})
                </TabsTrigger>
              ))}
            </TabsList>
          </Tabs>

          {/* Workflows Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {filteredWorkflows.map((workflow, index) => (
              <motion.div
                key={workflow.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
              >
                <Card className="h-full hover:shadow-lg transition-all duration-300 cursor-pointer group"
                      onClick={() => {
                        trackWorkflowView(workflow.id);
                        setSelectedWorkflow(workflow);
                      }}>
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <div className="flex items-center space-x-3">
                        <div className="text-blue-600 group-hover:text-blue-700">
                          {workflow.icon}
                        </div>
                        <div>
                          <CardTitle className="text-lg group-hover:text-blue-600 transition-colors">
                            {workflow.title}
                          </CardTitle>
                          <div className="flex items-center space-x-2 mt-2">
                            <Badge className={difficultyColors[workflow.difficulty]}>
                              {difficultyLabels[workflow.difficulty]}
                            </Badge>
                            <Badge variant="outline">
                              <Clock className="w-3 h-3 mr-1" />
                              {workflow.duration}
                            </Badge>
                          </div>
                        </div>
                      </div>
                      <div className="text-right">
                        <div className="flex items-center text-yellow-500 mb-1">
                          <Star className="w-4 h-4 mr-1" />
                          <span className="text-sm font-medium">{workflow.popularity}%</span>
                        </div>
                        <div className="text-xs text-gray-500"></div>
                      </div>
                    </div>
                  </CardHeader>
                  
                  <CardContent>
                    <p className="text-gray-600 mb-4">
                      {workflow.description}
                    </p>
                    
                    <div className="space-y-3">
                      <div>
                        <h4 className="text-sm font-medium text-gray-900 mb-2"></h4>
                        <div className="flex flex-wrap gap-1">
                          {workflow.targetAudience.slice(0, 3).map((audience, i) => (
                            <Badge key={i} variant="secondary" className="text-xs">
                              {audience}
                            </Badge>
                          ))}
                        </div>
                      </div>
                      
                      <div>
                        <h4 className="text-sm font-medium text-gray-900 mb-2"></h4>
                        <div className="flex flex-wrap gap-1">
                          {workflow.tools.slice(0, 4).map((tool, i) => (
                            <Badge key={i} variant="outline" className="text-xs">
                              {tool}
                            </Badge>
                          ))}
                          {workflow.tools.length > 4 && (
                            <Badge variant="outline" className="text-xs">
                              +{workflow.tools.length - 4}
                            </Badge>
                          )}
                        </div>
                      </div>
                    </div>
                    
                    <div className="mt-4 pt-4 border-t flex items-center justify-between">
                      <span className="text-sm text-gray-500">
                        {workflow.steps.length} 
                      </span>
                      <Button variant="outline" size="sm" className="group-hover:bg-blue-600 group-hover:text-white">
                        
                        <ArrowRight className="w-4 h-4 ml-1" />
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              </motion.div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
}

// Workflow Detail Component
function WorkflowDetail({ 
  workflow, 
  onBack 
}: { 
  workflow: Workflow; 
  onBack: () => void;
}) {
  const { trackUserAction } = useAnalytics();
  const [completedSteps, setCompletedSteps] = useState<string[]>([]);

  const handleStartWorkflow = () => {
    // 
    trackUserAction('start_workflow', { 
      workflowId: workflow.id,
      workflowTitle: workflow.title,
      difficulty: workflow.difficulty
    });
    
    // 
    const confirmStart = window.confirm(
      ` ${workflow.title}\n\n` +
      `  ${workflow.steps.length} \n` +
      ` ${workflow.duration}\n` +
      ` ${workflow.tools.slice(0, 3).join(', ')}${workflow.tools.length > 3 ? '' : ''}\n\n` +
      ``
    );
    
    if (confirmStart) {
      // 
      const firstStep = workflow.steps[0];
      if (firstStep && firstStep.tools.length > 0) {
        // 
        const toolLinks: { [key: string]: string } = {
          'ChatGPT': 'https://chat.openai.com',
          'Notion AI': 'https://notion.so',
          'Canva': 'https://canva.com',
          'Midjourney': 'https://midjourney.com',
          '': 'https://gaoding.com',
          '': 'https://newrank.cn',
          '': 'https://index.weixin.qq.com',
          '': 'https://index.baidu.com'
        };
        
        const toolToOpen = firstStep.tools.find(tool => toolLinks[tool]);
        if (toolToOpen) {
          window.open(toolLinks[toolToOpen], '_blank');
        }
      }
      
      // 
      alert(` \n\n`);
    }
  };

  const handleCompleteStep = (stepId: string) => {
    if (!completedSteps.includes(stepId)) {
      setCompletedSteps([...completedSteps, stepId]);
      trackUserAction('complete_step', {
        workflowId: workflow.id,
        stepId: stepId,
        completedSteps: completedSteps.length + 1,
        totalSteps: workflow.steps.length
      });
    }
  };
  return (
    <div className="min-h-screen bg-gray-50">
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="flex items-center space-x-4 mb-6">
            <Button variant="outline" onClick={onBack}>
               
            </Button>
            <div className="text-blue-600">
              {workflow.icon}
            </div>
            <h1 className="text-3xl font-bold text-gray-900">
              {workflow.title}
            </h1>
          </div>
          
          <div className="flex items-center space-x-4 mb-4">
            <Badge className={difficultyColors[workflow.difficulty]}>
              {difficultyLabels[workflow.difficulty]}
            </Badge>
            <Badge variant="outline">
              <Clock className="w-3 h-3 mr-1" />
              {workflow.duration}
            </Badge>
            <div className="flex items-center text-yellow-500">
              <Star className="w-4 h-4 mr-1" />
              <span className="text-sm font-medium">{workflow.popularity}%</span>
            </div>
          </div>
          
          <p className="text-lg text-gray-600 mb-6">
            {workflow.description}
          </p>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg"></CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {workflow.benefits.map((benefit, i) => (
                    <li key={i} className="flex items-start space-x-2">
                      <CheckCircle className="w-5 h-5 text-green-500 flex-shrink-0 mt-0.5" />
                      <span className="text-sm text-gray-600">{benefit}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>
            
            <Card>
              <CardHeader>
                <CardTitle className="text-lg"></CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  {workflow.targetAudience.map((audience, i) => (
                    <Badge key={i} variant="secondary" className="mr-2 mb-2">
                      {audience}
                    </Badge>
                  ))}
                </div>
              </CardContent>
            </Card>
            
            <Card>
              <CardHeader>
                <CardTitle className="text-lg"></CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  {workflow.tools.map((tool, i) => (
                    <Badge key={i} variant="outline" className="mr-2 mb-2">
                      {tool}
                    </Badge>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-2xl font-bold text-gray-900"></h2>
            <div className="text-sm text-gray-600">
              {completedSteps.length} / {workflow.steps.length}
            </div>
          </div>
          
          {/*  */}
          <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
            <div 
              className="bg-blue-600 h-2 rounded-full transition-all duration-300"
              style={{ 
                width: `${workflow.steps.length > 0 ? (completedSteps.length / workflow.steps.length) * 100 : 0}%` 
              }}
            ></div>
          </div>
        </div>
        
        <div className="space-y-6">
          {workflow.steps.map((step, index) => (
            <Card key={step.id}>
              <CardHeader>
                <div className="flex items-center space-x-4">
                  <div className={`flex items-center justify-center w-8 h-8 rounded-full font-bold transition-colors ${
                    completedSteps.includes(step.id) 
                      ? 'bg-green-600 text-white' 
                      : 'bg-blue-600 text-white'
                  }`}>
                    {completedSteps.includes(step.id) ? '' : index + 1}
                  </div>
                  <div>
                    <CardTitle className="text-xl">{step.title}</CardTitle>
                    <div className="flex items-center space-x-2 mt-2">
                      <Badge className={difficultyColors[step.difficulty]} variant="secondary">
                        {difficultyLabels[step.difficulty]}
                      </Badge>
                      <Badge variant="outline">
                        <Clock className="w-3 h-3 mr-1" />
                        {step.duration}
                      </Badge>
                    </div>
                  </div>
                </div>
              </CardHeader>
              
              <CardContent>
                <p className="text-gray-600 mb-4">
                  {step.description}
                </p>
                
                <div className="mb-4">
                  <h4 className="text-sm font-medium text-gray-900 mb-2"></h4>
                  <div className="flex flex-wrap gap-2">
                    {step.tools.map((tool, i) => (
                      <Badge key={i} variant="outline">
                        {tool}
                      </Badge>
                    ))}
                  </div>
                </div>
                
                <div className="flex items-center justify-between pt-4 border-t">
                  <div className="flex items-center space-x-2">
                    {completedSteps.includes(step.id) ? (
                      <>
                        <CheckCircle className="w-5 h-5 text-green-500" />
                        <span className="text-sm text-green-600 font-medium"></span>
                      </>
                    ) : (
                      <>
                        <div className="w-5 h-5 border-2 border-gray-300 rounded-full"></div>
                        <span className="text-sm text-gray-500"></span>
                      </>
                    )}
                  </div>
                  
                  <Button
                    variant={completedSteps.includes(step.id) ? "secondary" : "outline"}
                    size="sm"
                    onClick={() => handleCompleteStep(step.id)}
                    disabled={completedSteps.includes(step.id)}
                  >
                    {completedSteps.includes(step.id) ? " " : ""}
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
        
        <div className="mt-8 text-center">
          <Button 
            size="lg" 
            className="bg-blue-600 hover:bg-blue-700"
            onClick={handleStartWorkflow}
          >
            <Play className="w-5 h-5 mr-2" />
            
          </Button>
        </div>
      </div>
    </div>
  );
}