import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { 
  RotateCcw, 
  Play, 
  Trophy, 
  Star,
  Zap,
  Bot,
  User,
  Users,
  RefreshCw
} from 'lucide-react';
import { Button } from '../../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import SEOHead from '../../components/SEOHead';

interface BoardState {
  squares: (string | null)[];
  isXNext: boolean;
  winner: string | null;
  isDraw: boolean;
}

type GameMode = 'pve' | 'pvp'; // pve: , pvp: 

export default function AITicTacToe() {
  const [board, setBoard] = useState<BoardState>({
    squares: Array(9).fill(null),
    isXNext: true,
    winner: null,
    isDraw: false
  });
  const [gameMode, setGameMode] = useState<GameMode>('pve'); // 
  const [difficulty, setDifficulty] = useState<'easy' | 'medium' | 'hard'>('medium');
  const [gameStats, setGameStats] = useState({
    playerWins: 0,
    aiWins: 0,
    draws: 0
  });
  const [gameMessage, setGameMessage] = useState('');

  // 
  const calculateWinner = (squares: (string | null)[]) => {
    const lines = [
      [0, 1, 2], [3, 4, 5], [6, 7, 8], // 
      [0, 3, 6], [1, 4, 7], [2, 5, 8], // 
      [0, 4, 8], [2, 4, 6] // 
    ];
    
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    
    return null;
  };

  // 
  const checkDraw = (squares: (string | null)[]) => {
    return squares.every(square => square !== null) && !calculateWinner(squares);
  };

  // AI
  const getAIMove = (squares: (string | null)[], difficulty: string) => {
    const availableMoves = squares
      .map((square, index) => square === null ? index : null)
      .filter(index => index !== null) as number[];

    if (availableMoves.length === 0) return null;

    switch (difficulty) {
      case 'easy':
        // 
        return availableMoves[Math.floor(Math.random() * availableMoves.length)];
      
      case 'medium':
        // 
        if (Math.random() > 0.3) {
          return getBestMove(squares);
        } else {
          return availableMoves[Math.floor(Math.random() * availableMoves.length)];
        }
      
      case 'hard':
        // 
        return getBestMove(squares);
      
      default:
        return availableMoves[Math.floor(Math.random() * availableMoves.length)];
    }
  };

  // 
  const getBestMove = (squares: (string | null)[]) => {
    // AI
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        const newSquares = [...squares];
        newSquares[i] = 'O';
        if (calculateWinner(newSquares) === 'O') {
          return i;
        }
      }
    }
    
    // 
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        const newSquares = [...squares];
        newSquares[i] = 'X';
        if (calculateWinner(newSquares) === 'X') {
          return i;
        }
      }
    }
    
    // 
    if (squares[4] === null) {
      return 4;
    }
    
    // 
    const corners = [0, 2, 6, 8];
    const availableCorners = corners.filter(index => squares[index] === null);
    if (availableCorners.length > 0) {
      return availableCorners[Math.floor(Math.random() * availableCorners.length)];
    }
    
    // 
    const availableMoves = squares
      .map((square, index) => square === null ? index : null)
      .filter(index => index !== null) as number[];
    
    return availableMoves[Math.floor(Math.random() * availableMoves.length)];
  };

  // 
  const handleClick = (index: number) => {
    // 
    if (board.winner || board.isDraw || board.squares[index]) {
      return;
    }

    // 
    const newSquares = [...board.squares];
    newSquares[index] = board.isXNext ? 'X' : 'O';
    
    const winner = calculateWinner(newSquares);
    const isDraw = checkDraw(newSquares);
    
    // 
    setBoard({
      squares: newSquares,
      isXNext: !board.isXNext,
      winner,
      isDraw
    });
    
    // 
    if (winner) {
      if (gameMode === 'pve') {
        if (winner === 'X') {
          setGameMessage('');
          setGameStats(prev => ({ ...prev, playerWins: prev.playerWins + 1 }));
        } else {
          setGameMessage('AI');
          setGameStats(prev => ({ ...prev, aiWins: prev.aiWins + 1 }));
        }
      } else {
        if (winner === 'X') {
          setGameMessage('X');
          setGameStats(prev => ({ ...prev, playerWins: prev.playerWins + 1 }));
        } else {
          setGameMessage('O');
          setGameStats(prev => ({ ...prev, aiWins: prev.aiWins + 1 }));
        }
      }
      return;
    }
    
    if (isDraw) {
      setGameMessage('');
      setGameStats(prev => ({ ...prev, draws: prev.draws + 1 }));
      return;
    }
    
    // 
    if (gameMode === 'pve' && !board.isXNext) {
      // AI
      setGameMessage('AI...');
      
      // AI
      setTimeout(() => {
        const aiMove = getAIMove(newSquares, difficulty);
        if (aiMove !== null) {
          const aiSquares = [...newSquares];
          aiSquares[aiMove] = 'O';
          
          const aiWinner = calculateWinner(aiSquares);
          const aiIsDraw = checkDraw(aiSquares);
          
          setBoard({
            squares: aiSquares,
            isXNext: true,
            winner: aiWinner,
            isDraw: aiIsDraw
          });
          
          if (aiWinner) {
            setGameMessage('AI');
            setGameStats(prev => ({ ...prev, aiWins: prev.aiWins + 1 }));
          } else if (aiIsDraw) {
            setGameMessage('');
            setGameStats(prev => ({ ...prev, draws: prev.draws + 1 }));
          } else {
            setGameMessage('');
          }
        }
      }, 800);
    } else {
      // 
      setGameMessage(board.isXNext ? 'X' : 'O');
    }
  };

  // AI
  useEffect(() => {
    return () => {}; // 
  }, []);

  // 
  const resetGame = () => {
    setBoard({
      squares: Array(9).fill(null),
      isXNext: true,
      winner: null,
      isDraw: false
    });
    setGameMessage('');
  };

  // 
  const renderSquare = (index: number) => {
    return (
      <motion.button
        whileHover={{ scale: board.squares[index] ? 1 : 1.05 }}
        whileTap={{ scale: 0.95 }}
        className={`w-24 h-24 bg-white border-2 rounded-xl flex items-center justify-center text-4xl font-bold shadow-md transition-all duration-200 ${
          board.squares[index] === 'X' 
            ? 'text-blue-600 border-blue-300' 
            : board.squares[index] === 'O'
            ? 'text-red-600 border-red-300'
            : 'text-gray-400 border-gray-200 hover:border-blue-300 hover:text-blue-400'
        }`}
        onClick={() => handleClick(index)}
        disabled={!!board.winner || board.isDraw || !!board.squares[index]}
      >
        {board.squares[index] === 'X' ? (
          <User className="w-12 h-12" />
        ) : board.squares[index] === 'O' ? (
          <Bot className="w-12 h-12" />
        ) : null}
      </motion.button>
    );
  };

  return (
    <>
      <SEOHead 
        title="AI -  | WSNAIL.COM"
        description="AIAI"
        keywords="AI,,,,WSNAIL"
        url="https://wsnail.com/games/ai-tic-tac-toe"
        canonical="https://wsnail.com/games/ai-tic-tac-toe"
      />
      
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <Card className="bg-white/80 backdrop-blur-sm shadow-lg border-0">
            <CardHeader className="text-center pb-6">
              <CardTitle className="text-3xl font-bold text-gray-900 mb-4">
                AI
              </CardTitle>
              
              <div className="flex flex-wrap justify-center gap-6 mb-4">
                <div className="flex items-center space-x-2">
                  <User className="w-5 h-5 text-blue-600" />
                  <span className="font-semibold">X: {gameStats.playerWins}</span>
                </div>
                <div className="flex items-center space-x-2">
                  {gameMode === 'pve' ? (
                    <>
                      <Bot className="w-5 h-5 text-purple-600" />
                      <span className="font-semibold">AI: {gameStats.aiWins}</span>
                    </>
                  ) : (
                    <>
                      <User className="w-5 h-5 text-red-600" />
                      <span className="font-semibold">O: {gameStats.aiWins}</span>
                    </>
                  )}
                </div>
                <div className="flex items-center space-x-2">
                  <RefreshCw className="w-5 h-5 text-yellow-600" />
                  <span className="font-semibold">: {gameStats.draws}</span>
                </div>
                <div className="flex items-center space-x-2">
                  <div className="flex items-center">
                    {gameMode === 'pve' ? (
                      <>
                        <Bot className="w-5 h-5 text-gray-600 mr-1" />
                        <span className="font-semibold"></span>
                      </>
                    ) : (
                      <>
                        <Users className="w-5 h-5 text-gray-600 mr-1" />
                        <span className="font-semibold"></span>
                      </>
                    )}
                  </div>
                </div>
              </div>
              
              <div className="flex flex-wrap justify-center gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    
                  </label>
                  <div className="flex gap-2">
                    {(['easy', 'medium', 'hard'] as const).map((level) => (
                      <Button
                        key={level}
                        variant={difficulty === level ? 'default' : 'outline'}
                        onClick={() => setDifficulty(level)}
                        className="capitalize"
                      >
                        {level === 'easy' ? '' : level === 'medium' ? '' : ''}
                      </Button>
                    ))}
                  </div>
                </div>
                
                <div className="flex items-end">
                  <Button
                    onClick={resetGame}
                    variant="outline"
                    className="flex items-center"
                  >
                    <RotateCcw className="w-4 h-4 mr-2" />
                    
                  </Button>
                </div>
              </div>
            </CardHeader>
            
            <CardContent className="text-center">
              {/*  */}
              <div className="mb-8">
                <div className="inline-flex items-center px-4 py-2 bg-blue-100 text-blue-800 rounded-full">
                  <Star className="w-5 h-5 mr-2" />
                  <span className="font-semibold">{gameMessage}</span>
                </div>
              </div>
              
              {/*  */}
              <div className="flex justify-center mb-8">
                <div className="bg-gray-100 p-4 rounded-2xl">
                  <div className="grid grid-cols-3 gap-2">
                    {Array(9).fill(null).map((_, index) => (
                      <div key={index} className="p-1">
                        {renderSquare(index)}
                      </div>
                    ))}
                  </div>
                </div>
              </div>
              
              {/*  */}
              {(board.winner || board.isDraw) && (
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="mb-8"
                >
                  <Card className="bg-gradient-to-r from-blue-500 to-purple-600 text-white p-6">
                    <CardTitle className="text-2xl mb-2 flex items-center justify-center">
                      {board.winner === 'X' ? (
                        <>
                          <User className="w-8 h-8 mr-2" />
                          {gameMode === 'pve' ? '' : 'X'}
                        </>
                      ) : board.winner === 'O' ? (
                        <>
                          {gameMode === 'pve' ? (
                            <Bot className="w-8 h-8 mr-2" />
                          ) : (
                            <User className="w-8 h-8 mr-2" />
                          )}
                          {gameMode === 'pve' ? 'AI' : 'O'}
                        </>
                      ) : (
                        <>
                          <Trophy className="w-8 h-8 mr-2" />
                          
                        </>
                      )}
                    </CardTitle>
                    <p className="text-blue-100">
                      {board.winner === 'X' 
                        ? (gameMode === 'pve' ? 'AI' : 'X')
                        : board.winner === 'O' 
                        ? (gameMode === 'pve' ? 'AI' : 'O')
                        : ''}
                    </p>
                  </Card>
                </motion.div>
              )}
              
              {/*  */}
              <div className="bg-gray-50 rounded-lg p-6 text-left">
                <h3 className="text-lg font-semibold text-gray-900 mb-3"></h3>
                <ul className="space-y-2 text-gray-600">
                  <li className="flex items-start">
                    <Zap className="w-5 h-5 text-blue-500 mr-2 mt-0.5 flex-shrink-0" />
                    <span>
                      {gameMode === 'pve' ? (
                        <> <User className="w-4 h-4 inline text-blue-600" /> AI <Bot className="w-4 h-4 inline text-red-600" /> </>
                      ) : (
                        <>X <User className="w-4 h-4 inline text-blue-600" /> O <User className="w-4 h-4 inline text-red-600" /> </>
                      )}
                    </span>
                  </li>
                  <li className="flex items-start">
                    <Zap className="w-5 h-5 text-blue-500 mr-2 mt-0.5 flex-shrink-0" />
                    <span></span>
                  </li>
                  <li className="flex items-start">
                    <Zap className="w-5 h-5 text-blue-500 mr-2 mt-0.5 flex-shrink-0" />
                    <span>
                      {gameMode === 'pve' 
                        ? 'AI' 
                        : ''}
                    </span>
                  </li>
                </ul>
                
                <div className="mt-4 pt-4 border-t border-gray-200">
                  <Button
                    onClick={() => setGameMode(gameMode === 'pve' ? 'pvp' : 'pve')}
                    variant="outline"
                    className="flex items-center mx-auto"
                  >
                    {gameMode === 'pve' ? (
                      <>
                        <Users className="w-4 h-4 mr-2" />
                        
                      </>
                    ) : (
                      <>
                        <Bot className="w-4 h-4 mr-2" />
                        
                      </>
                    )}
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </>
  );
}