export interface ToolReview {
  id: string;
  toolName: string;
  category: string;
  rating: number; // 1-5
  pros: string[];
  cons: string[];
  useCases: string[];
  alternativeTools: string[];
  pricingModel: 'free' | 'freemium' | 'paid' | 'subscription';
  priceRange: string;
  learnDifficulty: 'easy' | 'medium' | 'hard';
  lastUpdated: string;
  reviewContent: {
    overview: string;
    features: {
      name: string;
      description: string;
      rating: number;
    }[];
    userExperience: string;
    performanceAnalysis: string;
    valueForMoney: string;
    conclusion: string;
  };
  screenshots?: string[];
  videoDemo?: string;
  author: {
    name: string;
    expertise: string;
  };
  tags: string[];
}

