import React from 'react';
import { Helmet } from 'react-helmet-async';

interface SEOHeadProps {
  title?: string;
  description?: string;
  keywords?: string;
  image?: string;
  url?: string;
  type?: string;
  structuredData?: object;
  canonical?: string;
}

const SEOHead: React.FC<SEOHeadProps> = ({
  title = "AI - 106+AI | AIAIAI | WSNAIL.COM",
  description = "WSNAIL.COM - 106+AIAIAIAIAI6AIAIAI",
  keywords = "AI,AI,AI,,AI,AI,AI,AI,AI,AI,AI,ChatGPT,AI,AI,WSNAIL",
  image = "https://wsnail.com/images/og-image.jpg",
  url = "https://wsnail.com/",
  type = "website",
  structuredData,
  canonical
}) => {
  return (
    <Helmet>
      {/*  */}
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      
      {/* Canonical URL */}
      {canonical && <link rel="canonical" href={canonical} />}
      
      {/* Open Graph  */}
      <meta property="og:type" content={type} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:url" content={url} />
      <meta property="og:image" content={image} />
      <meta property="og:site_name" content="WSNAIL.COM - AI" />
      <meta property="og:locale" content="zh_CN" />
      
      {/* Twitter Card  */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="@wsnail" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
      
      {/*  */}
      {structuredData && (
        <script type="application/ld+json">
          {JSON.stringify(structuredData)}
        </script>
      )}
    </Helmet>
  );
};

export default SEOHead;