import { DivinationResult } from '../types';

// AI
interface AIService {
  name: string;
  available: boolean;
  generateResponse(prompt: string, options?: any): Promise<string>;
}

// Gemini API
class GeminiAIService implements AIService {
  name = 'Gemini';
  available = true;
  private apiKey: string | undefined = import.meta.env.VITE_GEMINI_API_KEY;

  async generateResponse(prompt: string, options?: any): Promise<string> {
    if (!this.apiKey) {
      throw new Error('Gemini API key is not configured.');
    }
    
    try {
      // Google Generative AI
      const { GoogleGenerativeAI } = await import('@google/generative-ai');
      const genAI = new GoogleGenerativeAI(this.apiKey);
      const model = genAI.getGenerativeModel({ model: 'gemini-1.5-flash' });

      const result = await model.generateContent(prompt);
      const response = await result.response;
      const text = response.text();
      
      if (!text || text.trim() === '') {
        throw new Error('Empty response from Gemini API');
      }
      
      return text;
    } catch (error) {
      console.error('Gemini API:', error);
      // 
      throw new Error(`AI: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }
}

// AI - 
class FallbackAIService implements AIService {
  name = 'Fallback';
  available = true;

  async generateResponse(prompt: string, options?: any): Promise<string> {
    // prompt
    if (prompt.includes('') || prompt.includes('tarot')) {
      return ` \n\n\n \n \n \n\n`;
    }
    
    if (prompt.includes('') || prompt.includes('zodiac')) {
      return ` \n\n\n  \n  \n  \n  \n\n`;
    }
    
    if (prompt.includes('') || prompt.includes('bagua')) {
      return ` \n\n\n \n \n \n\n\n\n`;
    }
    
    if (prompt.includes('') || prompt.includes('palm')) {
      return ` \n\n\n\n\n\n\n\n\n`;
    }
    
    if (prompt.includes('') || prompt.includes('character')) {
      return ` \n\n\n\n\n\n\n\n\n`;
    }
    
    if (prompt.includes('') || prompt.includes('jiugong')) {
      return ` \n\n\n \n \n \n \n \n\n\n1. \n2. \n3. \n\n`;
    }
    
    // 
    if (prompt.includes('') || prompt.includes('')) {
      return ` \n\n\n\n\n1.  - \n2.  - \n3.  - \n4.  - \n\n`;
    }
    
    // 
    return ` AI\n\n\n\n1. \n2. \n3. \n4. \n\n`;
  }
}

// AI
export class AIServiceManager {
  private services: AIService[] = [
    new GeminiAIService(),
    new FallbackAIService()
  ];

  private conversationHistory: Array<{role: string, content: string, timestamp: Date}> = [];

  // AI
  getAvailableService(): AIService | null {
    return this.services.find(service => service.available) || null;
  }

  // API
  async generateResponse(prompt: string, options?: any): Promise<string> {
    let lastError: Error | null = null;
    
    // 
    for (const service of this.services) {
      if (!service.available) continue;
      
      try {
        const response = await service.generateResponse(prompt, options);
        
        // 
        this.conversationHistory.push(
          { role: 'user', content: prompt, timestamp: new Date() },
          { role: 'assistant', content: response, timestamp: new Date() }
        );

        return response;
      } catch (error) {
        console.error(`${service.name} :`, error);
        lastError = error as Error;
        
        // Gemini
        if (service.name !== 'Gemini') {
          service.available = false;
        }
      }
    }
    
    // 
    throw lastError || new Error('AI');
  }

  // 
  async generateContinuousResponse(prompt: string, useHistory: boolean = true): Promise<string> {
    let fullPrompt = prompt;
    
    if (useHistory && this.conversationHistory.length > 0) {
      const recentHistory = this.conversationHistory.slice(-6); // 6
      const historyContext = recentHistory.map(msg => 
        `${msg.role === 'user' ? '' : 'AI'}: ${msg.content}`
      ).join('\n');
      
      fullPrompt = `:\n${historyContext}\n\n: ${prompt}\n\n`;
    }

    return this.generateResponse(fullPrompt);
  }

  // 
  async generateProfessionalTarotReading(question: string, spread: string): Promise<DivinationResult> {
    const prompt = `"${question}"${spread === 'single' ? '' : ''}`;
    
    try {
      const interpretation = await this.generateResponse(prompt);
      return {
        type: 'tarot',
        result: '',
        interpretation,
        advice: this.extractAdvice(interpretation),
        timestamp: new Date().toISOString(),
        metadata: { question, spread }
      };
    } catch (error) {
      // 
      return {
        type: 'tarot',
        result: '',
        interpretation: ' ',
        advice: '',
        timestamp: new Date().toISOString(),
        metadata: { question, spread }
      };
    }
  }

  // 
  async generateAdvancedZodiacReading(birthDate: string, birthTime: string, birthPlace: string): Promise<DivinationResult> {
    const prompt = `${birthDate}${birthTime}${birthPlace}`;
    
    try {
      const interpretation = await this.generateResponse(prompt);
      return {
        type: 'zodiac',
        result: '',
        interpretation,
        advice: this.extractAdvice(interpretation),
        timestamp: new Date().toISOString(),
        metadata: { birthDate, birthTime, birthPlace }
      };
    } catch (error) {
      return {
        type: 'zodiac',
        result: '',
        interpretation: ' ',
        advice: '',
        timestamp: new Date().toISOString(),
        metadata: { birthDate, birthTime, birthPlace }
      };
    }
  }

  // 
  async generateProfessionalBaguaReading(question: string, method: string): Promise<DivinationResult> {
    const prompt = `${method === 'time' ? '' : ''}"${question}"`;
    
    try {
      const interpretation = await this.generateResponse(prompt);
      return {
        type: 'bagua',
        result: '',
        interpretation,
        advice: this.extractAdvice(interpretation),
        timestamp: new Date().toISOString(),
        metadata: { question, method }
      };
    } catch (error) {
      return {
        type: 'bagua',
        result: '',
        interpretation: ' ""',
        advice: '',
        timestamp: new Date().toISOString(),
        metadata: { question, method }
      };
    }
  }

  // 
  private extractAdvice(interpretation: string): string {
    // 
    const sentences = interpretation.split(/[.]/).filter(s => s.trim());
    const adviceSentences = sentences.filter(s => 
      s.includes('') || s.includes('') || s.includes('') || 
      s.includes('') || s.includes('') || s.includes('')
    );
    
    if (adviceSentences.length > 0) {
      return adviceSentences[0].trim();
    }
    
    // 
    return sentences.length > 0 ? sentences[sentences.length - 1].trim() : '';
  }

  // 
  getConversationHistory() {
    return this.conversationHistory;
  }

  // 
  clearConversationHistory() {
    this.conversationHistory = [];
  }

  // 
  resetServices() {
    this.services.forEach(service => {
      if (service.name !== 'Gemini') { // Gemini
        service.available = true;
      }
    });
  }
}

// 
export const aiServiceManager = new AIServiceManager();